import React from "react";

import { name } from "../../package.json";

const navbar = () => {
  return (
    <nav className="bg-gray-100 text-gray-700 py-3 px-5 w-screen mb-5">
      <div className="max-w-7xl mx-auto">
        <div className="lg:flex justify-between">
          <div className="flex justify-between items-center space-x-2">
            <a href="#" className="flex items-center px-2 py-2">
              <svg
                className="h-12 w-12 mr-1"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M20 7l-8-4-8 4m16 0l-8 4m8-4v10l-8 4m0-10L4 7m8 4v10M4 7v10l8 4"
                />
              </svg>
              <span className="text-3xl font-semibold">{name}</span>
            </a>
            <input
              className="h-10 rounded-full px-5 bg-gray-300 text-gray-900 placeholder-gray-900"
              type="text"
              placeholder="search items here..."
            />
          </div>

          <div className="hidden lg:flex items-center space-x-5 text-xl">
            <div className="flex items-center space-x-4">
              <a href="#">Home</a>
              <a href="#">Explore</a>
              <a href="#">Pages</a>
              <a href="#">Stats</a>
              <a href="#">Elements</a>
            </div>

            <div className="bg-purple-700 text-gray-100 rounded-xl px-2 py-1">
              Connect Wallet
            </div>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default navbar;
